import { Component, OnInit } from '@angular/core';
import { Order } from './order';
//import {orderjson} from './order.json';


@Component({
  selector: 'app-order-details',
  templateUrl: './order-details.component.html',
  styleUrls: ['./order-details.component.css']
})
export class OrderDetailsComponent implements OnInit {

  constructor() { }


//bottom Extra data
messagejob = ''; messagecontact = ''; messagephone="";messageinvoice ="";messagenotefordrive="";messagenoteforoffice="";

  someClickHandler(info: any): void {
    this.messagejob = info.Job ;
    this.messagecontact = info.Contact;
    this.messagephone = info.Phone;
    this.messageinvoice = info.Invoice;
    this.messagenotefordrive = info.NoteForDrive;
    this.messagenoteforoffice = info.NoteForOffice;
  }
//bottom Extra data


  dtOptions: DataTables.Settings = {};

  ngOnInit() {
    this.dtOptions = {
      ajax: 'assets/order.json',
      columns: [{
        title: 'Sno',
        data: 'ID',
      }, {
        title: 'Customer Name',
        data: 'CustomerName'
      }, {
        title: 'Cust Reference',
        data: 'CustReference'
      }, {
        title: 'Drop Off Date',
        data: 'DropoffDate'
      }, {
        title: 'Drop Off Time',
        data: 'DropoffTime'
      }, {
        title: 'Pick Up Date',
        data: 'PickUpDate'
      }, {
        title: 'Pick Up Time',
        data: 'PickUpTime'
      }, {
        title: 'Bin type',
        data: 'BinType'
      }, {
        title: 'Number needed',
        data: 'Numberneeded'
      }, {
        title: 'Amount',
        data: 'Amount'
      }],
      responsive: true,
      rowCallback: (row: Node, data: any[] | Object, index: number) => {
        const self = this;
      
        // Unbind first in order to avoid any duplicate handler
        // (see https://github.com/l-lin/angular-datatables/issues/87)
        $('td', row).unbind('click');
        $('td', row).bind('click', () => {
          self.someClickHandler(data);
          
        });
        
        return row;
      },
     // select: true
     
    };
  }
  }


